import noData from '@/components/noData'
import axios from 'axios'
import back from '@/components/back'
export default {
	components: {
		noData,
		back
	},
	data() {
		return {
			switchState: true,
			switchStateo: true,
			info: null,
			serviceContent: [],
			setmeal: [],
			setmealShow: false,
			content:"",
			descriptionList:[],
			drMsg:null,
			Evaluate: [],
			teamMsg:null,
			total:0,
			flag:false,
			temp:false,
			getInfo:null,
			list: [],
			page:1,
			haveMore:true,
			user: this.$store.state.userInfo,
			patient: this.$store.state.patient
		}
	},
	created() {
		if(this.$store.state.serviceChoose){
			this.info = this.$store.state.serviceChoose
		}else{
			this.$toast("请先选择你需要推荐的服务包")
			return this.$router.back()
		}
		this.serviceContent = this.info.Details;
		this.descriptionList = this.info.Description.split(/\r\n|↵|\n/g)
		if(this.info.ServiceType == 2){
			//基础服务包
			this.getBasicBag()
		}else if(this.info.DrId > 0 && this.info.FieldType == 2){
			//专项服务包，跟医生挂钩，医生个人服务包
			//获取医生详情
			//获取服务包的详情
			axios.all([
				this.$get('/PlatFormAPI/Doctor/QueryDoctorInfoByDrId', {
					DrId: this.info.DrId
				})
			]).then(o => {
				this.drMsg = o[0];
			})
		}
	},
	mounted() {
		
	},
	beforeRouteLeave(to,from,next){
        if(to.path != "/good-deatils"){
        	//如果不是进入商品详情页就清楚选中的服务包
        	this.$store.dispatch('pushServiceChoose', null)
        }
	    next()
    },
	beforeDestroy(){
		
	},
	methods: {
		getBasicBag(){
			axios.all([
				this.$post('PlatFormAPI/FamilyDoctorTeam/GetFamilyDoctorTeams', {
					ServicePageId: this.info.ServicePackId,
					OrgId: this.info.OrgId,
					AreaId:0,
					TeamName:"",
					PageIndex:this.page,
					PageSize:10,
				})
			]).then(o => {
				this.teamMsg = o[0].ReturnData || []
				this.temp = true
				if(!o[0].ReturnData || o[0].ReturnData.length <10){
					this.haveMore = false
				}else{
					this.haveMore = true
				}
			})
		},
		getMoreDrTeams(){
			this.page ++
			this.$post('/PlatFormAPI/FamilyDoctorTeam/GetFamilyDoctorTeams', {
				ServicePageId: this.info.ServicePackId,
				OrgId: this.info.OrgId,
				AreaId:0,
				TeamName:"",
				PageIndex:this.page,
				PageSize:10,
			}).then( o => {
				if(!o.ReturnData || o.ReturnData.length < 10){
					this.haveMore = false
				}else{
					this.haveMore = true
				}
				if(o.ReturnData){
					o.ReturnData.forEach(v => {
						this.teamMsg.push(v)
					})
				}
			})
		},
		sendPack(){
			const i = this.info
            const pack = {
                "Title": i.ServicePackName,
                "Content":[
                    {
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text":"服务总价",
                            "Detail": `${i.Price}元`
                        }
                    }
                ],
                "ExtData":{
                    "Type":32,
                    "Data":{
                        "serviceId": i.ServicePackId,
            			"DrId": i.DrId,
            			"DrName":i.DrName,
            			"ReComDrId":this.user.Data.User.DoctorId,
            			"ReComDrName":this.user.Data.User.DoctorName
                    }//
                },
                "IsSystem":false
            }
            i.Details = i.Details || []
            i.Details.forEach(v => {
            	if(v.GoodsSource && (v.GoodsSource == 2 || v.GoodsSource == 3)){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `x${v.NumValue}`
                        }
                    })
                }
            	else if(v.SericeItemCode == '1701'){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": "1份"
                        }
                    })
                }
                else if(v.NumType != 7  && v.NumType != 8 && v.NumType != 9 && v.NumType != null){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.NumValue}${v.NumTypeName}`
                        }
                    })
                }
                else if(v.NumType == 9 || v.NumType == null){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.TotalPrice}元`
                        }
                    })
                }
                else if(v.NumType == 7 || v.NumType == 8){
                    pack.Content.push({
                        "Type":"TextDetail",
                        "ContentElem":{
                            "Text": v.ServiceItemName,
                            "Detail": `${v.NumTypeName}`
                        }
                    })
                }
            })
            
            //获取推荐记录id
            this.$post('/PlatFormAPI/ServicePackItem/SavePackRecommendRecord',{
                RecomRecordId: 0,
                DrId: this.user.Data.User.DoctorId,
                DrName:this.user.Data.User.DoctorName,
                ServicePackId:i.ServicePackId,
                ServicePackName:i.ServicePackName,
                PatientId: this.patient.PatientId,
                PatientName: this.patient.PatientName,
            }).then(o => {
            	pack.ExtData.Data.RecomRecordId = o.RecomRecordId
            	this.$store.dispatch('pushServiceChoose', null)
                this.$bridge.callhandler('ServicePack', JSON.stringify(pack))
            	this.$bridge.callhandler('Back')
            })
		},
		preview(e){
			let arr = []
			if(e.target.src){
				arr[0] = e.target.src
				const data = {
	                position: 0,
	                url: arr
	            }
				this.$bridge.callhandler('Preview', JSON.stringify(data))
			}
       },
		contentDetails(i) {
			this.setmeal = i;
			/*
			 GoodsSource:商品类型(1-平台服务，2-专科商品，3-平台商品，4-医生自定义)
			 * */
				
			if(this.setmeal.GoodsSource == 2 || this.setmeal.GoodsSource == 3){
				this.$router.push({
					path:"/good-deatils",
					query:{
						id:i.ServiceItemId, //商品id
						specialistId:0,//专科商品id
					}
				})
			}else{
				this.setmealShow = true;
				this.content = this.setmeal.Description ? this.setmeal.Description.replace(/\r\n|↵|\n/g,'<br/>'):"";
				this.$refs.content.innerHTML = this.content
				if(!this.getInfo && this.setmeal.SericeItemCode && this.setmeal.SericeItemCode == '1501'){
					this.$get('/FetalHeartAPI/Monitor/GetContactInfo', {
		                orgId: this.info.OrgId
		            }).then( o => {   //团队
						this.getInfo = o
					})
				}
			}
			
		}
	}
}



